.selectoutlined {
  align-self: stretch;
  flex: 1;
}
.inputsRow {
  flex: 1;
  display: flex;
  flex-direction: row;
  padding: var(--padding-8xs);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.button {
  position: relative;
  font-size: var(--components-button-large-size);
  letter-spacing: 0.46px;
  line-height: 26px;
  text-transform: uppercase;
  font-weight: 500;
  font-family: var(--components-button-large);
  color: var(--primary-contrast);
  text-align: center;
}
.searchFlightsButton {
  cursor: pointer;
  border: 0;
  padding: 0 var(--padding-3xs);
  background-color: var(--color-orange-200);
  border-radius: var(--br-9xs);
  width: 164px;
  height: 56px;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.buttonGroup,
.formInputsRow {
  display: flex;
  align-items: flex-start;
}
.buttonGroup {
  flex-direction: column;
  padding: var(--padding-8xs);
  justify-content: center;
}
.formInputsRow {
  align-self: stretch;
  flex-direction: row;
  justify-content: flex-start;
}
@media screen and (max-width: 1100px) {
  .inputsRow {
    width: 100%;
    flex: unset;
    align-self: stretch;
  }
  .buttonGroup {
    width: 100%;
    text-align: left;
  }
  .formInputsRow {
    flex-direction: column;
  }
}
@media screen and (max-width: 650px) {
  .selectoutlined {
    flex: unset;
    align-self: stretch;
  }
  .inputsRow {
    flex-direction: column;
  }
  .searchFlightsButton {
    width: 100%;
  }
  .searchFlightsButton:hover {
    background-color: var(--color-darkgoldenrod-200);
  }
}
