.whereAreYou {
  align-self: stretch;
  position: relative;
  line-height: 67px;
}
.title {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.searchFlights {
  flex: 1;
  position: relative;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}
.flightOptions {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-xl);
}
.formTitleGroup,
.searchForm {
  align-self: stretch;
  justify-content: flex-start;
}
.formTitleGroup {
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: var(--padding-8xs);
  align-items: center;
  gap: var(--gap-8xs);
}
.searchForm {
  border-radius: var(--br-3xs);
  background-color: var(--primary-contrast);
  padding: var(--padding-xl);
  align-items: flex-start;
  gap: var(--gap-9xs);
  font-size: var(--font-size-xl);
  color: var(--color-darkslategray-300);
  font-family: var(--components-button-large);
}
.searchContainer,
.searchForm,
.searchSection {
  display: flex;
  flex-direction: column;
}
.searchSection {
  width: 100%;
  padding: 0 var(--padding-61xl);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  gap: var(--gap-xl);
}
.searchContainer {
  align-self: stretch;
  padding: var(--padding-40xl) 0;
  align-items: flex-start;
  justify-content: flex-start;
  background-image: url(/public/search-container1@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  text-align: left;
  font-size: var(--font-size-42xl);
  color: var(--primary-contrast);
  font-family: var(--font-baloo-bhai);
}
@media screen and (max-width: 1100px) {
  .whereAreYou {
    font-size: var(--font-size-23xl);
    line-height: 48px;
  }
  .searchForm {
    flex-direction: column;
  }
  .searchSection {
    padding-left: var(--padding-11xl);
    padding-right: var(--padding-11xl);
    box-sizing: border-box;
    width: 100%;
    top: calc(44% - 96.5px - 0px);
  }
}
@media screen and (max-width: 650px) {
  .whereAreYou {
    font-size: var(--font-size-12xl);
    line-height: 40px;
  }
  .searchFlights {
    width: 100%;
    padding-bottom: var(--padding-3xs);
    margin-bottom: 6px;
    border-bottom: 1px;
    border-bottom-style: solid;
    border-bottom-color: #eaeaea;
    flex: unset;
    align-self: stretch;
  }
  .flightOptions {
    width: 100%;
  }
  .formTitleGroup {
    flex-direction: column;
    align-items: flex-start;
  }
  .searchForm {
    margin-top: 20px;
  }
  .searchSection {
    width: 100%;
    top: calc(32% - 96.5px - 0px);
  }
}
